<template>
  <div class="audit_wrap">
    <listSearch v-model="searchData" :searchArr="searchArr" @searchConfirm="goSearch"></listSearch>
    <div class="audit_table">
      <div class="audit_button">
        <Button type="primary" @click="edit(true)">批量修改仲裁员</Button>
        <Button type="primary" @click="submitPerson(true)">批量同意</Button>
      </div>
      <Table 
      @on-select="selectOne"
      @on-select-cancel="selectCancel"
      @on-select-all="selectAll"
      @on-select-all-cancel="selectAllCancel"
      :columns="columns" 
      :data="tableData">
        <template slot-scope="{ row }" slot="1">
          <span style="color:#538FFF">{{row['1']}}</span>
        </template>
        <template slot-scope="{ row, index }" slot="action">
          <div>
             <a href="javascript:;" style="margin-right:20px" @click="edit(false,row)" title="修改仲裁员">
               <img src="../../assets/images/zhongcai.png" style="width:24px;height:24px;" alt>
             </a>
            <a href="javascript:;" title="同意仲裁员" @click="submitPerson(false,row)">
              <img src="../../assets/images/resolve.png" style="width:24px;height:24px;" alt></a>
          </div>
        </template>
      </Table>
     <Page style="margin-top:20px" 
      :current="currentPage" 
       :total="total"
       :page-size="pageSize"
       :page-size-opts="pageOpts" 
       @on-change="changeCurrentPage"
       @on-page-size-change="changeSize"
       show-sizer show-elevator show-total />
    </div>
    <!-- 批量修改 -->
    <Modal v-model="fileDialog" :footer-hide="true"  title="批量修改仲裁员">
      <RadioGroup v-model="vertical" vertical>
        <Radio :label="item.id" v-for="(item,index) in radioList" :key="index">
          <span>{{item.username}}</span>
        </Radio>
      </RadioGroup>
       <div style="border-top: 1px solid rgb(232, 234, 236);padding: 12px 18px;text-align: center;">
        <Button style="margin-right:20px;" @click="fileDialog=false">取消</Button>
        <Button type="primary" @click="handleEdit">确定</Button>
      </div>
    </Modal>
    <!-- 修改仲裁员 -->
    <Modal v-model="editShow" @on-ok="editSubmit" :footer-hide="true" width="1000" title="修改仲裁员">
      <div class="edit_person">
        <div class="left">
          <div class="item">
            <ul>
              <li>申请人：</li>
              <li>边裁：</li>
              <li>边裁：</li>
              <li>主裁：</li>
            </ul>
            <ul>
              <li>xxx</li>
              <li>xx</li>
              <li>xx</li>
              <li>xxx</li>
            </ul>
          </div>
          <div class="item">
            <ul>
              <li>被申请人：</li>
              <li>边裁：</li>
              <li>边裁：</li>
              <li>主裁：</li>
            </ul>
            <ul>
              <li>xxx</li>
              <li>xx</li>
              <li>xx</li>
              <li>xxx</li>
            </ul>
          </div>
          <div class="item">
            <ul>
              <li>部门领导：</li>
              <li>边裁：</li>
              <li>边裁：</li>
              <li>主裁：</li>
            </ul>
            <ul>
              <li>xxx</li>
              <li>xx</li>
              <li>xx</li>
              <li>xxx</li>
            </ul>
          </div>
          
        </div>
        <div class="right">
          <div class="header_box">
            <div class="title">选择仲裁员</div>
               <Input
                  prefix="ios-search"
                  v-model="selectPerson"
                  placeholder="姓名或首字母"
                  style="width:200px"
                />
          </div>
          <div class="person_type">
            <ul>
              <li>主裁：<a href="#">选择仲裁员</a></li>
              <li>边裁：<a href="#">选择仲裁员</a></li>
              <li>边裁：<a href="#">选择仲裁员</a></li>
            </ul>
          </div>
          <Table  class="file_table" :columns="personColumns" :data="personTable">
                <template slot-scope="{ row,index}" slot="action">
                   <Checkbox v-model="row.checked" @on-change="handleCheckbox(row,index)"></Checkbox>
                </template>
            </Table>
            <Page :current="1" :total="personTable.length" :page-size="personTable.length" />
        </div>
      </div>
      <div style="border-top: 1px solid rgb(232, 234, 236);padding: 12px 18px;text-align: center;">
        <Button style="margin-right:20px;" @click="handleCancel">取消</Button>
        <Button type="primary" @click="editSubmit">确定</Button>
      </div>
    </Modal>
  </div>
</template>
<script>
import listSearch from "@/components/list-search/search.vue";
import { listMixin } from "./mixin";
import { mapActions } from "vuex";
let columns = [
  {
    type: "selection",
    width: 100,
    align: "center"
  },
  {
    title: "案号",
    key: "code",
    align: "center",
    width: 200
  },
  {
    title: "案件类型",
    key: "caseTypeName",
    align: "center"
  },
  {
    title: "申请人",
    key: "applicantName",
    align: "center"
  },
  {
    title: "申请人仲裁员",
    key: "applicantArbName",
    align: "center"
  },
  {
    title: "被申请人",
    key: "respondentName",
    align: "center"
  },
  {
    title: "被申请人仲裁员",
    key: "respondentArbName",
    align: "center"
  },
  {
    title: "最终仲裁员",
    key: "finalArbName",
    align: "center"
  },
  {
    title: "开庭时间",
    key: "beginTime",
    align: "center"
  },
  {
    title: "操作",
    slot: "action",
    width: 150,
    align: "center"
  }
];
export default {
  components: {
    listSearch
  },
  mixins: [listMixin],
  data() {
    return {
      currentPage: 1,
      pageSize: 10,
      total: 10,
      searchData: {
        number: "",
        person: "",
        name: [],
        type: []
      },
      searchArr: [
        {
          type: "input",
          value: "number",
          label: "案号",
          placeholder: "请输入案号"
        },
        {
          type: "input",
          value: "person",
          label: "申请人",
          placeholder: "请输入申请人"
        },
        {
          type: "input",
          value: "person",
          label: "被申请人",
          placeholder: "请输入被申请人"
        },
        {
          type: "select",
          value: "name",
          label: "注册名称",
          arr: [
            { label: "全部", value: 0 },
            { label: "通过", value: 1 },
            { label: "未通过", value: 2 }
          ],
          placeholder: "请选择"
        },
        {
          type: "select",
          value: "type",
          label: "案件类型",
          arr: [
            { label: "全部", value: 0 },
            { label: "合同纠纷", value: 1 },
            { label: "小额贷款", value: 2 },
            { label: "借贷纠纷", value: 3 }
          ],
          placeholder: "请选择"
        },
        {
          type: "checkbox",
          value: "type",
          label: "条件选择",
          arr: [
            { label: "全部", value: 0 },
            { label: "速裁", value: 1 },
            { label: "普裁", value: 2 }
          ]
        }
      ],
      columns: columns,
      fileDialog: false,
    
     
      back: false, // 驳回
      editShow: false,
      vertical: "",
      radioList: [], // 所有仲裁员
      selectedData: [], //复选框全选反选
      backInput: "",
      rejectReason: "",
      selectPerson: "", //选择仲裁员
      personColumns: [
        { title: "姓名", key: "username" },
        { title: "专业", key: "roleName" },
        { title: "选择", slot: "action" }
      ],
      personTable: [], //所有仲裁员
      requsetId:''
    };
  },
  mounted(){
    this.getAllPerson()
  },
  methods: {
    ...mapActions({
      getRequsetList: "createCase/getRequsetList",
      gelAllArbitrator:"replyPeriod/gelAllArbitrator", // 所有仲裁员
      getVerifyList: "organizePeriod/getVerifyList",
      distributePerson:"organizePeriod/distributePerson", // 分配仲裁员
      distributePersonAll:"organizePeriod/distributePersonAll", // 分配仲裁员
      surePerson:"organizePeriod/surePerson"
    }),
    

    goSearch() {},
    // 确定
    submitPerson(value, row){
      if(!row.applicantArbName ||!row.respondentArbName){
         this.$Message.warning('不能为空');
              return
            }
      this.$Modal.confirm({
          title: "同意",
          content:
            `<p>申请人仲裁员:  ${row.applicantArbName|| ''}</p>
            <p style="margin-top:5px">被申请人仲裁员:  ${row.respondentArbName|| ''}</p>`,
          onOk: async() => {
          
          const send = await  this.surePerson({actionIdentifer:"GROUP_AGREE_ARBITRATOR",entity_caseId:row.id});
             
            if (send.data.flag) {
                 this.$Message.success("操作成功");
                  this.getList()
                } else {
                  this.$Message.error(send.data.message);
                }
            
          },
          onCancel: () => {
          }
        });
    },
    async handleEdit() {
      this.$Message.success("修改成功");
     
          let arr = []
        this.selectedData.map(item=>{
             let params = {
                entity_caseId:item,
                arbitratorId: this.vertical,
                actionIdentifer:'GROUP_AGREE_ARBITRATOR'
              }
              arr.push(params)
        })
      const result =await this.distributePersonAll({arr:JSON.stringify(arr)})
      if (result.data.flag) {
          this.$Message.success("操作成功");
          this.fileDialog=false
          this.handleCancel()
          this.getList()
        } else {
          this.$Message.error(result.data.message);
        }
    },
    // 驳回提交
    backSubmit() {},
    // 编辑仲裁员 单个提交
    async editSubmit() {
      
      const arr = this.personTable.filter(item=>item.checked)
     
      let params = {
            entity_caseId:this.requsetId,
            arbitratorId: arr.length==1? arr[0].id: '',
            actionIdentifer:'GROUP_AGREE_ARBITRATOR'
          }
      const result = await this.distributePerson(params)
      if (result.data.flag) {
          this.$Message.success("操作成功");
          this.handleCancel()
          this.getList()
        } else {
          this.$Message.error(result.data.message);
        }
    },
    // value true 批量  flase 单个 
    edit(value,row) {
    
      if(value){
          if (this.selectedData.length === 0) {
            this.$Message.warning("请至少选择一条案件信息");
            return;
          }
          this.fileDialog = true;
      } else {
        this.requsetId=row.id;
         this.editShow = true;
      }     
    },
    handleCancel(){
      this.editShow=false
      this.requsetId=''
    },
    // 单选
    handleCheckbox(row, index) {
      // console.log(row,this.personTable[index]);
      let copy = JSON.parse(JSON.stringify(this.personTable));
      if (row.checked) {
        copy.map(item => {
          item.checked = false;
        });
        copy[index] = row;
        this.personTable = copy;
      }
    },
    async getList() {
      let params = {
        menuIdentifer: "APPROVE_GROUP_LIST", //菜单标识
        pageIndex: this.currentPage - 1,
        pageSize: this.pageSize
      };
      let filter = {
        //筛选条件
        applicantName: "", //申请人关键字
        respondentName: "", //被申请人关键字
        userId: "", //注册名称的用户id
        caseTypeId: "", //案件类型id
        createTimeBegin: "", //提交的开始时间
        createTimeEnd: "" //提交的结束时间
      };

      params = { ...params, filter: JSON.stringify({}) };
      // console.log("参数", params);
      const res = await this.getRequsetList(params);
      if (res.data.flag) {
        this.tableData = res.data.data.dataList;
        this.total = res.data.data.totalCount;
        this.selectedData = []; // 重置
      }
    },
    async getAllPerson(){
      const result =await this.gelAllArbitrator()
      if(result.data.flag){
        this.personTable=result.data.data;
        this.radioList=result.data.data;
      }
    }
  }
};
</script>
<style lang="less" scoped>
/deep/.ivu-table th {
  background-color: #3296fa;
}
/deep/thead .ivu-table-cell-with-selection::after {
  content: "全选";
  position: relative;
  left: 5px;
}
/deep/.ivu-modal-footer {
  text-align: center;
}

.audit_wrap {
  .audit_table {
    background-color: #fff;
    margin-top: 30px;
    padding: 20px;
    .audit_button {
      display: flex;
      justify-content: flex-end;
      padding-right: 20px;
      margin-bottom: 20px;
      button:first-child {
        margin-right: 10px;
      }
    }
  }
}
.reject_content {
  .title {
    font-size: 16px;
    font-family: PingFangSC-Regular, PingFang SC;
    font-weight: 400;
    color: #708094;
    margin-bottom: 8px;
  }
}

.edit_person {
  display: flex;
  padding: 0 50px;
  .left {
    color: #515a6e;
    font-size: 12px;
    .item {
      display: flex;
      margin-bottom: 5px;
      padding-bottom: 5px;
      border-bottom: 1px solid #e2ecfc;
      ul:first-child {
        text-align: right;
      }
      ul:last-child {
        text-align: left;
      }
      &:last-child {
        border: none;
      }
    }
  }
  .right {
    flex: 1;
    margin-left: 30px;
    .header_box {
      display: flex;
      justify-content: space-between;
      align-items: center;
    }
    .person_type {
      margin-top: 10px;
      ul {
        display: flex;
        justify-content: space-between;
      }
      a {
        color: #3296fa;
        text-decoration: underline;
      }
    }
    .file_table {
      margin: 10px 0;
    }
  }
}
</style>
